@import "colors";

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    background-color: $back-color;
}

.container{
    color: #fff;
    align-items: center;
    padding: 30px 10%;
    h1{
        text-decoration: underline solid $princepal-color;
        font-size: 60px;
        color: $princepal-color;
    }

    h2{
        font-size: 25px;
        margin-top: 25px;

        a{
            text-decoration: underline solid $princepal-color;
            color: $princepal-color;
            font-size: 30px;
        }
    }
}